.kiteForecastTable {
    width: 600px;
    height: 300px;
}

h1 {
    font-size: 5rem;
    opacity: 0.6;
    text-align: center;

}
.kiteWeatherContainer {
    background: url('./kitesmain1.jpg');
    background-position: center;
    background-size:cover;
    background-repeat: no-repeat;
    height: 100vh;
    width: 100%;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    object-fit: cover;
}

.kiteAppContainter h2 {
    color: pink;
    font-size: 80px;
    margin-top: -120px;
}

.kiteAppContainter p {
    margin-top: 8px;
    font-size: 32px;
    color: blue;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.homepageform {
display:  flex;
flex-direction: column;
justify-content: center;
align-items: center;
}

.inputSelect {
    width: 500px;
    padding: 15px;
    border: none;
    border-radius: 30px;
    box-shadow: inset 0 5px 5px #0000001a;
    display: block;
    box-sizing: border-box;
    background: #F72585;
    color: white;
    font-size: medium;
    margin-top: 30px;
    margin-bottom: 20px;
}

.selectedKitespotWrapper{
    display: flex;
    flex-direction: row;
}

.iframeFrontPage{
    background-color: white;
    width: 300px;
    height: 300px;
}

.selectedKiteSpotContent{
    background-color:blue;
    width: 300px;
    height: 300px;
}


.weatherResults {
    display: block;
    padding: 10px;
    margin-top: 30px;
    color: white;
    font-size:1rem;

}